@import "../../styles/main";

.pictures-slider {
  &__container {
    @include onTablet {
      margin: 0 auto;
      padding: 0 24px;
    }

    @include onDesktop {
      padding: 0;
      max-width: 1136px;
    }
  }

  &__slides-container {
    width: 100%;
    overflow: hidden;
  }

  &__slides {
    display: flex;
    width: 300%;
    height: 100%;
    transition: transform .5s ease;
  }

  &__list {
    display: flex;
    width: 100%;
    height: 100%;
  }

  &__item {
    width: 100%;
    height: 100%;
    list-style: none;
    display: inline-block;
  }

  &__link {
    display: block;
    width: 100%;
    padding-bottom: 38.5%;
    background-size: cover;
    background-position: center;
  }

  &__arrow-container {
    display: none;

    @include onTablet {
      display: block;
    }

    &:last-child {
      @include onTablet {
        justify-self: end;
      }
    }
  }

  &__arrow {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid black;
    width: 32px;
    height: 100%;
  }

}
